@page {
  size: auto;
  margin: 0;
}

@lost gutter 0;
html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.clearfix::before, .clearfix::after {
  display: table;
  content: ' ';
}

.clearfix::after {
  clear: both;
}

.img_responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.icon {
  display: block;
}

.icon svg {
  display: block;
  width: 100%;
  height: 100%;
}

.icon img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
}

