.gibs {
  min-height: 0;
}

.gibs .gibs--page_title,
.gibs .gibs_buttons,
.gibs .gibs_form,
.gibs .gibs--disclaimer {
  display: none;
}

.gibs .gibs--img {
  display: block;
  height: 50vh;
  border-bottom: 1rem solid #D3D3DF;
  padding: 4rem;
  min-height: 0;
}

.gibs .gibs--img img {
  opacity: 1;
  max-height: 100%;
}

.gibs .gibs--content {
  display: block;
  min-height: 0;
  padding: 4rem;
}

.gibs .gibs--expiration {
  color: #908FA1;
}
